#sliding-popup {
  font-size: 14px;
  text-align: center;
}
#sliding-popup .list-inline > li {
  margin-top: 11px;
  vertical-align: middle;
}
#sliding-popup .find-more-button {
  color: #fff;
}
.jumbotron {
  margin-bottom: 45px;
}
.jumbotron h1 {
  margin-top: 0;
}
.jumbotron .text-primary {
  color: #0095f3;
}
.jumbotron .form-control-feedback {
  color: #666;
}
#search-form .form-control {
  font-size: 16px;
}
.panel {
  border-width: 2px;
}
.panel-tour .tour-title {
  margin-top: 22px;
}
.panel-resources .list-resources {
  margin-bottom: 0;
}
.panel-resources .list-resources > dd {
  margin-bottom: 11px;
}
@media (max-width: 991px) {
  .panel-tour:last-child {
    margin-bottom: 22px;
  }
}
@media (min-width: 992px) {
  .row-flex,
  .col-flex {
    display: flex;
  }
  .panel-tour {
    align-items: center;
    display: flex;
  }
}
.footer {
  background-color: #eee;
  border-color: #ddd;
  color: #808080;
  font-size: 14px;
}
.footer a {
  color: #808080;
}
.footer a:hover,
.footer a:focus {
  color: #666;
}
#block-iucn-frontend-footer {
  float: right;
}
#block-iucn-frontend-footer .nav > li > a:hover,
#block-iucn-frontend-footer .nav > li > a:focus {
  text-decoration: underline;
}
